          给排水实验室成立于1987年，实验室总面积约670平方米，设备固定资产约566万元，800元以上仪器设备253台；实验室有兼职实验教师8人，专职实验员1人，其中，具有博士学历6人，硕士学历3人。本实验室主要仪器设备有：气相色谱-质谱联用仪，液相色谱仪、气相色谱仪，离子色谱分析仪、原子荧光光度计、傅里叶变换红外光谱仪、定量PCR分析仪、胶体电位测定仪、紫外光分光光度计、荧光摄影显微镜、净化工作台、生化培养箱、活性炭吸附装置、曝气充氧装置、过滤实验装置、离子交换吸附装置等一批水处理实验仪器和装置。
          目前，给排水实验室主要承担给排水科学与工程专业3门专业课（水处理生物学、水分析化学、水处理工程实验）的实验教学工作。本实验室为本科生实验教学、本科生创新训练项目、研究生实验教学、科学研究等提供服务。经过多年努力，给排水实验室所形成的研究方向包括供水安全保障技术、废水生物处理及资源化技术、微量污染物的环境行为与生态修复技术、固体废弃物资源化利用技术等。给排水实验室以本专业教师的科研项目带动本科生和研究生的教学和科研创新，支撑给排水科学与工程专业的学科建设和发展。
